import './shared.css';
import styles from './footer.css';

import React, {Component} from 'react';

export default class Footer extends Component {

  handleLinkClick(url, e) {
    e.preventDefault();
    window.open(url, '_blank');
  }

  render() {
    const data = this.props.data;
    return (
      <footer className={styles.footer}>
        <div className={styles.content}>
          <a href="/" className={styles.linkHome}>{data.title}</a>
          <ul className={styles.links}>
            { data.links.map( function m(item, i) {
              return (
                <li className={styles.linkItem} key={i}>
                  <a href="#" className={styles.link} onClick={this.handleLinkClick.bind(this, item.url)}>
                    {item.label}
                  </a>
                </li>
              );
            }, this)}
          </ul>
        </div>
      </footer>
    );
  }
}

Footer.propTypes = {
  data: React.PropTypes.object
};


