import './shared.css';
import styles from './work.css';

import React, {Component} from 'react';

const images = {
  reactInspire: require('../assets/work/reactinspire.png'),
  hailo: require('../assets/work/hailo.png'),
  hailoPages: require('../assets/work/hailo-pages.png'),
  hailoMobile: require('../assets/work/hailo-mobile.png'),
  times: require('../assets/work/times.png'),
};

export default class Work extends Component {
  render() {
    return (
      <div>
        <section className={styles.section}>
          <div className={styles.content}>
            <h3>React Inspire</h3>
            <p><a href="http://reactinspire.com" title="React Inspire">React
            Inspire</a> is a side-project, a gallery of curated sites built with
            React. It's an early/alpha version done over the weekend. Built with React, Webpack and
             CSS Modules. The source code is also available on <a href="https://github.com/reactinspire/reactinspire.github.io">GitHub</a>.
            </p>
          </div>
          <div className={styles.preview}>
            <img src={images.reactInspire} alt="React Inspire" />
          </div>
        </section>
        <section className={styles.section}>
          <div className={styles.content}>
            <h3>Hailo</h3>
            <p><a href="https://www.hailoapp.com" title="Hailo">Hailo</a> is a
            free mobile app for book taxis or private cars. I built the website,
            the front-end framework for the web apps and the style-guide. We
            released the first version within 10 weeks. The site is fully
            static generated using Jekyll, Sass e Node.</p>
          </div>
          <div className={styles.preview}>
            <img src={images.hailo} alt="Hailo" />
            <img src={images.hailoPages} alt="Hailo" />
            <img src={images.hailoMobile} alt="Hailo" />
          </div>
        </section>
        <section className={styles.section}>
          <div className={styles.content}>
            <h3>Times Collection</h3>
            <p><a href="http://magflow.timesdev.tools/#/my-lives">Times
            Collection</a> is a publishing tool for online magazines built
            initially for The Times iPad app edition. It was an exciting project and
            opportunity to work closely with the design team helping them with the UX, grid
            layout and typography. I built whole project in about 4 months, both the
            Django backend and the front-end using Ember.js</p>
          </div>
          <div className={styles.preview}>
            <img src={images.times} alt="Times Collection" />
          </div>
        </section>
      </div>
    );
  }
}

Work.propTypes = {
  data: React.PropTypes.object
};



