import './shared.css';
import styles from './hero.css';

import React, {Component} from 'react';

export default class Hero extends Component {
  render() {
    const data = this.props.data;
    return (
      <header className={styles.hero}>
        <div className={styles.content}>
          <p>Ola! My name is {data.title}, I'm {data.role}. I like product design, UI prototyping,
          and front-end architecture. Currently prototyping FX/trading
          tools at <a href="https://www.rbs.co.uk">Royal Bank of Scotland</a>
          .</p>
          <p>My previous work experience also includes <a href="https://www.hailoapp.com">Hailo</a> (2014), <a href="http://www.thetimes.co.uk">
          The Times</a> (2013-2014), <a href="http://www.samsung.com/uk">Samsung</a> (2012) and <a href="http://www.b-reel.com/">B-Reel</a> (2011).</p>
          <p>The front-end stack I'm digging is React, Webpack and PostCSS. Although I've done
          projects with Ember.js, Backbone, Gulp and Sass. For backend I'm using Node/MongoDB and previously working with Python/Django.</p>
        </div>
      </header>
    );
  }
}

Hero.propTypes = {
  data: React.PropTypes.object
};



