import styles from './app.css';

import React, {Component} from 'react';
import {RouteHandler} from 'react-router';

export default class App extends Component {
  safeStringify(obj) {
    return JSON.stringify(obj).replace(/<\/script/g, '<\\/script').replace(/<!--/g, '<\\!--');
  }
  render() {
    const initialProps = { __html: this.safeStringify(this.props) };
    const typescript = { __html: 'try{Typekit.load({ async: true });}catch(e){}' };
    return (
      <html>
        <head>
          <meta charSet="UTF-8" />
          <meta httpEquiv="X-UA-Compatible" content="IE=edge,chrome=1" />
          <meta name="description" content="" />
          <meta name="keywords" content="" />
          <meta name="author" content="Henrique Alves <henriquea@gmail.com> (http://alves.im)" />
          <meta name="viewport" content="width=device-width, initial-scale=1" />
          <title>{this.props.title}</title>
          <link rel="stylesheet" href="/styles.css" type="text/css" />
          <script src="//use.typekit.net/omg3pif.js"></script>
          <script dangerouslySetInnerHTML={typescript}></script>
        </head>
        <body className={styles.body}>
          <RouteHandler {...this.props} />
          <script
            id="initial-props"
            type="application/json"
            dangerouslySetInnerHTML={initialProps}>
          </script>
          <script src="/bundle.js"></script>
        </body>
      </html>
    );
  }
}

App.propTypes = {
  title: React.PropTypes.string
};




