import './shared.css';
import styles from './info.css';

import React, {Component} from 'react';

export default class Info extends Component {
  render() {
    return (
      <header className={styles.info}>
        <div className={styles.content}>
          <p>Apart from the selected work above I recently built <a href="http://alves.im/minigrid">Minigrid</a>.
          Ocasionally I speak in conferences and give full-day workshops on front-end architecture. The
           next one is going to happen in <strong>Moscow on 31 Oct &ndash; 01 Nov</strong>.
          </p>
        </div>
      </header>
    );
  }
}

Info.propTypes = {
  data: React.PropTypes.object
};



